import { default as React } from 'react';
import { useTranslation } from 'react-i18next';
import { educationData, skillsData, interestsData } from '../data';
import { MyGitHub, MyLinkedIn } from '../components/social';

const AboutPage = () => {
	const { t, i18n } = useTranslation();
	const edData = educationData[i18n.language];
	const skData = skillsData[i18n.language];
	const intsData = interestsData[i18n.language];

	return (
		<div className="page page--centered aboutPage">
			<div className="page__content">
				<h1 className="page__title">{t('pages:about')}</h1>

				<div className="aboutPage__intro">
					{/* <div className="aboutPage__introTitle">
					{t('content:aboutIntroTitle')}
				</div> */}
					{/* <div className="aboutPage__introParagraph"> */}
					{t('content:aboutIntro')}
					{/* </div> */}
				</div>

				<div className="aboutPage__links">
					<MyGitHub classes="aboutPage__link" />
					<MyLinkedIn classes="aboutPage__link" />
				</div>

				<div className="aboutPage__education education__container">
					<h2 className="page__paragraphTitle education__title">
						{t('content:aboutEducationTitle')}
					</h2>
					<ul className="education__list">
						{edData &&
							edData.map((ed) => (
								<li key={ed.specialization} className="education__item">
									<h3 className="education__subTitle">
										{ed.level} {ed.period}
									</h3>
									<p className="education__paragraph">
										<span className="education__specialization">
											{ed.specialization}
										</span>
										<br />@ {ed.school}
									</p>
								</li>
							))}
					</ul>
				</div>

				<div className="aboutPage__skills skills__container">
					<h2 className="page__paragraphTitle skills__title">
						{t('content:aboutSkillsTitle')}
					</h2>
					{skData && (
						<>
							<p className="skills__list">
								{skData.programmingLanguages && (
									<ul className="skills__programmingLanguages">
										<h3 className="skills__subTitle">
											{skData.programmingLanguages.title}
										</h3>
										{skData.programmingLanguages.list.map((pL) => (
											<li className="skills__skill">{pL}</li>
										))}
									</ul>
								)}
							</p>
							<p className="skills__list">
								{skData.programmingLanguages && (
									<ul className="skills__frameworksTools">
										<h3 className="skills__subTitle">
											{skData.frameworksTools.title}
										</h3>
										{skData.frameworksTools.list.map((tool) => (
											<li className="skills__skill">{tool}</li>
										))}
									</ul>
								)}
							</p>
							<p className="skills__list">
								{skData.programs && (
									<ul className="skills__programs">
										<h3 className="skills__subTitle">
											{skData.programs.title}
										</h3>
										{skData.programs.list.map((program) => (
											<li className="skills__skill">{program}</li>
										))}
									</ul>
								)}
							</p>
							<p className="skills__list">
								{skData.languages && (
									<ul className="skills__languages">
										<h3 className="skills__subTitle">
											{skData.languages.title}
										</h3>
										{skData.languages.list.map((language) => (
											<li className="skills__skill">{language}</li>
										))}
									</ul>
								)}
							</p>
						</>
					)}
				</div>

				<div className="aboutPage__interests interests__container">
					<h2 className="page__paragraphTitle interests__title">
						{t('content:aboutInterestsTitle')}
					</h2>
					<ul className="skill__list">
						{intsData && (
							<ul className="skills__languages">
								{intsData.map((interest) => (
									<li className="skills__skill">{interest}</li>
								))}
							</ul>
						)}
					</ul>
				</div>
			</div>
		</div>
	);
};

export default AboutPage;
