import React from 'react';
import classnames from 'classnames';
import { FaLinkedin } from 'react-icons/fa';

export const MyLinkedIn = ({ classes }) => {
	return (
		<a href="https://www.linkedin.com/in/thomva/" className={classnames('link social__link', classes)}>
			<FaLinkedin />
		</a>
	);
};
